<!--üö®ÎßàÏùºÎ¶¨ÏßÄ Í¥ÄÎ¶¨Ïûê :  ÎßàÏùºÎ¶¨ÏßÄ ÏàòÏ†ïÌôîÎ©¥-->
<template>
  <div class="cards page-back mx-auto">
    <div class="d-flex p-4">
      <button type="button" @click="goBack">
        <p class="lg2">
          <i class="bi bi-chevron-left lg2 border border-secondary p-2 rounded-2"></i>&nbsp;&nbsp;Ïù¥Ï†Ñ
        </p>
      </button>
    </div>
    <div class="p-4 mt-5">
      <div class="d-flex justify-content-between">
        <h3 class="lg p-3" style="text-align: left; font-family: KB_C2">ÎßàÏùºÎ¶¨ÏßÄ Ìï≠Î™©</h3>
        <div class="d-flex justify-content-end align-items-center">
          <input type="radio" class="btn-check" name="job_name" id="Í≥µÌÜµ" value="Í≥µÌÜµ" v-model="selectedJob" autocomplete="off" checked>
          <label class="btn custom-radio-btn" for="Í≥µÌÜµ" style="width:5vw; font-size: 15pt; font-family: 'KB_C3';">Í≥µÌÜµ</label>
          <input type="radio" class="btn-check" name="job_name" id="Í∞úÏù∏" value="Í∞úÏù∏" v-model="selectedJob" autocomplete="off">
          <label class="btn custom-radio-btn" for="Í∞úÏù∏" style="width:5vw; font-size: 15pt; font-family: 'KB_C3';">Í∞úÏù∏</label>
          <input type="radio" class="btn-check" name="job_name" id="Í∏∞ÏóÖ" value="Í∏∞ÏóÖ" v-model="selectedJob" autocomplete="off">
          <label class="btn custom-radio-btn" for="Í∏∞ÏóÖ" style="width:5vw; font-size: 15pt; font-family: 'KB_C3';">Í∏∞ÏóÖ</label>
        </div>
      </div>
      <div class="p-4">
        <div class="input-gray p-4">
          <input type="text" class="lg2" v-model="mile_title" style="width: 100%; text-align: left;"/>
        </div>
      </div>
    </div>
    <div class="p-4">
      <h3 class="lg p-3" style="text-align: left; font-family: KB_C2">ÏÉÅÏÑ∏ ÎÇ¥Ïö©</h3>
      <div class="p-4">
        <div class="input-gray p-4">
          <textarea v-model="mile_content" class="lg2" style="text-align: left; width: 100%; height: 30vh"></textarea>
        </div>
      </div>
    </div>
    <div class="p-4">
      <h3 class="lg p-3" style="text-align: left; font-family: KB_C2">Ï≤®Î∂Ä ÌååÏùº</h3>
      <div class="p-4">
        <div class="d-flex input-gray p-4">
          <input type="file" @change="handlerFileUpload" class="md" style="width: 80%; text-align: right;"/>
          <span v-if="before_mile_route" class="md" style="text-align:left;">Í∏∞Ï°¥ ÌååÏùº: {{ before_mile_route}}</span>
        </div>       
      </div>
    </div>
    <div class="my-5">
      <button @click="updateAction" class="btn-green" style="width:8vw; height: 3vw; font-size:1.2vw; font-family: KB_C2;">ÏàòÏ†ï</button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'IntroduceMileageModifyAdminView',
  data(){
    return {
      mile_title: '',
      mile_content: '',
      file: null,
      before_mile_route: '',
      mile_introduce_no: null,
      mile_no: '',
      selectedJob: ''
    }
  },
  computed:{
    ...mapGetters('mile', ['getMileDetail']),
    ...mapGetters('login', ['getLoginInfo']),
    mileDetail(){
      return this.getMileDetail;
    },
    loginInfo(){
      return this.getLoginInfo;
    },
  },
  methods: {
    ...mapActions('mile', ['getMileModify', 'updateMile']),
    goBack() {
      this.$router.go(-1);
    },
    handlerFileUpload(event){
      this.file = event.target.files[0];
      this.mile_route = this.file ? this.file.name : ''; //ÏÑ†ÌÉùÎêú ÌååÏùºÏùò Ïù¥Î¶ÑÏùÑ Ï†ÄÏû• 
    },
    async updateAction(){
      const mileInfo = new FormData();
      mileInfo.append('mile_introduce_no', this.mile_introduce_no);
      mileInfo.append('mile_title', this.mile_title);
      mileInfo.append('job_name', this.selectedJob);
      mileInfo.append('mile_content', this.mile_content);
      if(this.file){
        mileInfo.append('file', this.file);
      } else {
        const response = await fetch(this.before_mile_route);
        const blob = await response.blob();
        const fileName = this.before_mile_route.split('/').pop();
        const file = new File([blob], fileName, {type: blob.type});
        mileInfo.append('file', file);
      }

      const response = await this.updateMile(mileInfo);
      if(response && response.data.success){
        this.showAlert('ÎßàÏùºÎ¶¨ÏßÄÍ∞Ä ÏàòÏ†ïÎêòÏóàÏäµÎãàÎã§', 'success', '/IntroduceMileageAdminView');
      }else{
        this.showAlert('ÎßàÏùºÎ¶¨ÏßÄ ÏàòÏ†ïÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§', 'error', '#');
      }
    },
    showAlert(t, i, r) {
      this.$swal({
        title: t,
        icon: i,
        scrollbarPadding: false
      }).then((result) => {
        if(result.isConfirmed && r !== '#'){
          this.$router.push(r);
        }
      })
    },
  },
  async created(){
    this.mile_no = this.loginInfo ? this.loginInfo.mile_no : null;
    this.mile_introduce_no = this.$route.query.mile_introduce_no;
    if(this.mile_introduce_no && this.mile_no){
      await this.getMileModify({
        mile_introduce_no: this.mile_introduce_no,
        mile_no: this.mile_no
      });
      const mileDetail = this.getMileDetail;
      if(mileDetail){
        this.mile_title = mileDetail.mile_title;
        this.selectedJob = mileDetail.job_name;
        this.mile_content = mileDetail.mile_content;
        this.before_mile_route = mileDetail.mile_route; // Í∏∞Ï°¥ ÌååÏùºÎ™ÖÏùÑ Í∞ÄÏ†∏ÏôÄÏÑú ÌëúÏãú
      }else{
        console.error('Ìï¥Îãπ ÎßàÏùºÎ¶¨ÏßÄ Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§.');
      }
    }else{
      console.error('mile_introduce_noÏù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§.');
    }
  }
};
</script>

<style scope>

/* Í∏∞Î≥∏ Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.custom-radio-btn {
  border: 1px solid transparent;
  transition: background-color 0.3s ease;
}

/* ÏÑ†ÌÉùÎêú Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.btn-check:checked + .custom-radio-btn {
  /* border-color: #19c99b; */
  background-color: #19c99b;
  color:#fff;
  font-weight: 600;
}

.btn-check:hover + .custom-radio-btn:hover {
  background-color: #19c99b;
  color:#fff;
  font-weight: 600;
}
</style>
